.ul-nav {

  display: flex;
  justify-content: flex-end;
}

.div-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.div-header > img {
  width: 150px;
  height: 150px;
}

.ul-nav {
  height: 30px;
}

.ul-nav > li {
  padding: 5px 40px;
  margin-right: 30px;
  margin-left: 30px;
  list-style: none;
  background: rgba( 36, 198, 220, 0.45 );
  border-radius: 6%;
}

.ul-nav > li:hover{
  background: rgba( 0, 0, 0, 0.15 );
  font-weight: bold;
}

@media only screen and (max-device-width: 425px) {
  body { width: 100%;}
  .div-header > img {
    width: 75px;
    height: 75px;
  }
  .ul-nav > li {
    padding: 5px 15px;
    margin-right: 10px;
    margin-left: 0px;
  }
}

@media screen and (min-width: 375px) and (max-width: 400px) {
  .ul-nav {
    margin-top: 80px;
    display: flex;
    margin-left: -50px;
    flex-direction: column;
  }
  .ul-nav > li {
    margin-bottom: 10px;
  }
}